import * as uiRouter from "angular-ui-router";
import Models from "../../models/models";
import AuthorizedPage from "../../pages/authorizedPage";
import CourseService from "../../services/courseService";
import app from "../app";

class CourseListPage extends AuthorizedPage {
    /**
     * 登録コンポーネント名
     */
    public static readonly IID: string = "auth.courselist";

    /**
     * コンポーネントオプション
     */
    protected static setOptions(options: ng.IComponentOptions) {
        options.templateUrl = "./courses/courseList.html";
    }

    /**
     * bindings変数リストにResolvedを追加する
     * @param options
     */
    protected static setInheritOptions(options: ng.IComponentOptions): void {
        super.setInheritOptions(options);
        this.setResolveBindings(options, CourseListPage.state);
    }

    /**
     * ui-uiRouterにおけるstate
     */
    public static readonly state: uiRouter.Ng1StateDeclaration =
    {
        url: "/courses",
        resolve: {
            courseList:
            [CourseService.IID, (courseService: CourseService) => {
                return courseService.getCourseList();
            }],
        },
    };

    public courseList: Models.Dtos.CourseDto[];

    // protected $onInit(): void {
    //     this
    // }

    // public constructor() {
    //     super();
    // }
}

CourseListPage.register(app.getModule());



