import * as uiRouter from "angular-ui-router";
import IdentityService from "../../services/IdentityService";
import app from "../app";
import CoursePage from "./course";

class CourseModulesPage extends CoursePage {
    /**
     * 登録コンポーネント名
     */
    public static readonly IID: string = "auth.course.modules";

    /**
     * コンポーネントオプション
     */
    protected static setOptions(options: ng.IComponentOptions) {
        options.templateUrl = "./courses/courseModules.html";
    }

    /**
     * ui-uiRouterにおけるstate
     */
    public static readonly state: uiRouter.Ng1StateDeclaration =
    {
        url: "/",
    };
}

CourseModulesPage.register(app.getModule());



