import * as uiRouter from "angular-ui-router";
import AuthorizedPage from "../../pages/authorizedPage";
import CourseService from "../../services/courseService";
import app from "../app";

export default class CoursePage extends AuthorizedPage {
    /**
     * 登録コンポーネント名
     */
    public static readonly IID: string = "auth.course";

    /**
     * コンポーネントオプション
     */
    protected static setOptions(options: ng.IComponentOptions) {
        options.template = `
<app-header title="$ctrl.courseService.course.name" back-to="'auth.courselist'" flex="none"></app-header>
<md-content flex="100" class="height100">
    <ui-view></ui-view>
</md-content>
`;
    }

    /**
     * bindings変数リストにResolvedを追加する
     * @param options
     */
    protected static setInheritOptions(options: ng.IComponentOptions): void {
        super.setInheritOptions(options);
        this.setResolveBindings(options, CoursePage.state);
    }

    /**
     * ui-uiRouterにおけるstate
     */
    public static readonly state: uiRouter.Ng1StateDeclaration =
    {
        url: "/courses/:courseId",
        abstract: true,
        resolve: {
            courseService:
            [CourseService.IID, "$transition$", (courseService: CourseService, $transition$: uiRouter.Transition) => {
                return courseService.load($transition$.params().courseId).then(
                    // success
                    (service) => service,
                );
            }],
        },
    };

    public courseService: CourseService;
}

CoursePage.register(app.getModule());



